
import Customer from "../../../Models/Customer";
import "./addCustomer_FC.css";
import { useForm } from "react-hook-form";
import { Button, ButtonGroup, TextField } from "@material-ui/core";
import jwtAxios from "../../../utils/JwtAxios";
function AddCustomer_FC(): JSX.Element {
    const { register, handleSubmit, watch, formState: { errors } } = useForm();
    function sendDetails(customerDetails: Customer){
        console.log(customerDetails)
        const url="http://localhost:8080/administrator/addCustomer";
        
       jwtAxios.post(url,customerDetails)
       .then(()=>{
            console.log("company was add");
            console.log(customerDetails);
            }
       )
       .catch(error=>{
        console.log(error.response)
       })
       
    }

    return (
        <div className="addCustomer_FC">
		<h1> Add Customer </h1>
            <form onSubmit={handleSubmit(sendDetails)}>
                <TextField  label="customer first_name" variant="filled" {...register("first_name")} /><br/><br/>
                <TextField  label="customer last_name" variant="filled" {...register("last_name")}/><br/><br/>
                <TextField  label="customer Email" variant="filled" {...register("email")}/><br/><br/>
                <TextField  label="customer password" variant="filled" {...register("password")}/><br/><br/>
                <ButtonGroup variant="contained" fullWidth>
                    <Button color="primary" type="submit">Add Customer</Button>
                </ButtonGroup>
                </form>	
        </div>
    );
}

export default AddCustomer_FC;
