import { Button, ButtonGroup, TextField } from "@material-ui/core";
import { Component, SyntheticEvent } from "react";
import Customer from "../../../../../Models/Customer";
import jwtAxios from "../../../../../utils/JwtAxios";
import SingleCustomer_CC from "../singleCustomer_CC/singleCustomer_CC";
import "./getOneCustomer_CC.css";

interface GetOneCustomer_CCProps {
	
}

interface GetOneCustomer_CCState {
	customerNumber:string,
    customer: Customer,
    show:boolean
}

class GetOneCustomer_CC extends Component<GetOneCustomer_CCProps, GetOneCustomer_CCState> {

    public constructor(props: GetOneCustomer_CCProps) {
        super(props);
        this.state = {
            customerNumber:"",
            customer:new Customer(),
            show:false
        };
    }

    private setValue = (args: SyntheticEvent) => {
        const value = (args.target as HTMLInputElement).value;
        this.setState({customerNumber: value});
    };
    private getCustomer=(args:SyntheticEvent)=>{
        console.log("customer number:");
        console.log(this.state.customerNumber);
        jwtAxios.get("http://localhost:8080/administrator/getOneCustomer/"+this.state.customerNumber)
        .then(response=>{
            console.log("my data:")
            console.log(response);
            this.setState({show:true});
            this.setState({customer:response.data})
        })
        .catch((error)=>{
            this.setState({show:false});
            console.log(error);
        })
    }

    public render(): JSX.Element {
        return (
            <div className="getOneCustomer_CC">
				  <h1> Get One customer</h1><br/>
				<TextField label="customer number" variant="outlined" onChange={this.setValue} value={this.state.customerNumber}/>
                <ButtonGroup variant="contained" onClick={this.getCustomer} >
                    <Button color="primary">Search</Button>
                </ButtonGroup>
                {this.state.show && <SingleCustomer_CC customer={this.state.customer}/>}
            </div>
        );
    }
}

export default GetOneCustomer_CC;
