                                                                                                                                                       import { Button, ButtonGroup, MenuItem, Select, TextField } from "@material-ui/core";
import { Category } from "@material-ui/icons";
import axios from "axios";
import { Component, InputHTMLAttributes, SyntheticEvent } from "react";
import Company from "../../../Models/Company";
import Coupon from "../../../Models/Coupon";
import jwtAxios from "../../../utils/JwtAxios";
import SingleCoupon_CC from "../../admin/src/Components/singleCoupon_CC/singleCoupon_CC";
import "./getAllCouponsByCompanyAndCategory_CC.css";

interface GetAllCouponsByCompanyAndCategory_CCProps {
	
}

interface GetAllCouponsByCompanyAndCategory_CCState {
    category:string,
	companyNumber:string,
    company:Company,
    show:boolean,
    coupon:Coupon[],
}

class GetAllCouponsByCompanyAndCategory_CC extends Component<GetAllCouponsByCompanyAndCategory_CCProps, GetAllCouponsByCompanyAndCategory_CCState> {
    private setValue = (args: SyntheticEvent) => {
        //const value=args.target.value; //who is args??? what is it type???
        const value = (args.target as HTMLInputElement).value;
        this.setState({companyNumber: value});
    };
    
    private getCompanyCoupons = (args: SyntheticEvent) => {
        let url = "http://localhost:8080/company/getAllCouponsByCompanyAndCategory/"+this.state.companyNumber+"/"+this.state.category
        console.log(url);
        jwtAxios.get("http://localhost:8080/company/getAllCouponsByCompanyAndCategory/"+this.state.companyNumber+"/"+this.state.category)
        .then((response)=>{
            this.setState({show:true});
            console.log(response.data)
            this.setState({coupon:response.data});
            
        })
        .catch((error)=>{
            this.setState({show:false});
            console.log(error);
        })
    }
    public constructor(props: GetAllCouponsByCompanyAndCategory_CCProps) {
        super(props);
        this.state = {
            category:"",
			companyNumber:"",
            company:new Company(),
            show:false,
			coupon:[]
        };
    }
    
    private categoryValue = (args:any) =>{
        console.log("my cat:"+args);
        const cat=(args.target as HTMLInputElement).value;
        console.log(cat);
        this.setState({category:cat})
    }
    
    public render(): JSX.Element {
        return (
            <div className="getAllCouponsByCompanyAndCategory_CC">
				    <h1>Get single company coupons by category</h1><hr/>
                <TextField label="company number" variant="outlined" onChange={this.setValue} value={this.state.companyNumber}/>
                <Select value={this.state.category} onChange={this.categoryValue} label="Category" labelWidth={100}>
                    <MenuItem value={"Food"}>Food</MenuItem>
                    <MenuItem value={"Clothing"}>Clothing</MenuItem>
                    <MenuItem value={"Vacation"}>Vacation</MenuItem>
                    <MenuItem value={"Electricity"}>Electricity</MenuItem>
                    <MenuItem value={"Sports"}>Sports</MenuItem>
                </Select><br/><br/>
                <ButtonGroup variant="contained" onClick={this.getCompanyCoupons} >
                    <Button color="primary">Search</Button>
                </ButtonGroup>
                {
				this.state.coupon.map(item =>
                        <SingleCoupon_CC key={item.id} coupon={item} showDelete={true}/>
                    )}
                <br/>
            </div>
        );
    }
}

export default GetAllCouponsByCompanyAndCategory_CC;
