import { Button, ButtonGroup, MenuItem, Select, TextField, Typography } from "@material-ui/core";
import { AccountBox } from "@material-ui/icons";
import axios from "axios";
import { useState } from "react";
import { useForm } from "react-hook-form";
import { useHistory } from "react-router-dom";
import UserDetails from "../../Models/UserDetails";
import Notify from "../../utils/Notify";
import "./login_FC.css";
import LockOpenIcon from '@material-ui/icons/LockOpen';
import AccessibilityNewIcon from '@material-ui/icons/AccessibilityNew';
import jwtAxios from "../../utils/JwtAxios";
import globals from "../../utils/globals";

function Login_FC(): JSX.Element {
    const {register, handleSubmit, setError,  formState: { errors }} = useForm<UserDetails>();
    const [jwtToken,setToken] = useState("User has no token, bad bad user !!!");
    const history = useHistory();

    function send(userDetails:UserDetails){
        console.log("data arraived")
        console.log(userDetails.clientType);    
        jwtAxios.post<string>(globals.urls.administrator+"adminLogin",userDetails) //will change if we in test or production
        .then((response)=>{
            console.log(response.headers);
            setToken(response.data);
            console.log(jwtToken);
           
        }) 
          
        .catch(error=>{
           
            const notify = new Notify();
            notify.error("you can touch this !!!")
            setToken("Error in getting response from the server");
           
        });
    }
    return (
        <div className="login_FC">
			  <form onSubmit={handleSubmit(send)}>
                    <Typography variant="h4" className="HeadLine">Login Form</Typography><br/>
                    <AccountBox style={{fontSize:40, margin :10}}/>
                    <TextField label="user" variant="outlined" 
                        {...register("email",{
                            required : {value : true, message : "field is required"}
                            ,minLength: {value : 5, message :"minimum length must be 5"}
                        })}/>
                    <span> {errors.email && <p>{errors.email.message}</p>}</span>
                    <br/><br/>
                    <LockOpenIcon style={{fontSize:40, margin :10}}/>
                    <TextField label="password" variant="outlined" type="password" 
                        {...register("password",{required : true, minLength:3, maxLength:10})}/>
                    <br/><br/>
                    <AccessibilityNewIcon style={{fontSize:40, margin:10}}/>
                    <Select style={{width:250}} {...register("clientType",{required : true})}>
                        <MenuItem value={"admin"} >System Administrator</MenuItem>
                        <MenuItem value={"company"}>Company</MenuItem>
                        <MenuItem value={"client"}>Client</MenuItem>
                    </Select>
                    <br/><br/>
                                
                    <ButtonGroup variant="contained" fullWidth>
                        <Button type="submit" color="primary">Send</Button>                    
                    </ButtonGroup>
                </form>
                <div className="smallBox">
                jwt token: <br/>
                {jwtToken}
            </div>
        </div>
    );
}

export default Login_FC;
