import { Button, ButtonGroup } from "@material-ui/core";
import axios from "axios";
import { Component, SyntheticEvent } from "react";
import Coupon from "../../../../../Models/Coupon";
import jwtAxios from "../../../../../utils/JwtAxios";
import "./singleCoupon_CC.css";

interface SingleCoupon_CCProps {
    coupon:Coupon,
    showDelete:boolean,
}

interface SingleCoupon_CCState {
	myCoupon:Coupon,
    showMore:boolean,
    
    
}

class SingleCoupon_CC extends Component<SingleCoupon_CCProps, SingleCoupon_CCState> {

    public constructor(props: SingleCoupon_CCProps) {
        super(props);
        this.state = {
			myCoupon:this.props.coupon,
            showMore:false,
        };
        console.log(this.props.coupon);
    }
    private deleteCoupon=(args:SyntheticEvent)=>{
           
            const url="http://localhost:8080/company/deleteCoupon/";
           
            jwtAxios.get(url+this.props.coupon.id)
            .then((response)=>{
                this.setState({myCoupon:response.data})
            })
            .catch((error)=>{
                console.log(error);
            })
           
    }

    private showMe = ()=>{
        let inv = !this.state.showMore; //false=>true true=>false
        this.setState({showMore: inv})
    }
    
    private size=25;
    public render(): JSX.Element {
        return (
            <div className="singleCoupon_CC CouponBox">
				{/*Coupon image:{this.props.coupon.image}<br/>*/}
                <img src={this.props.coupon.image} width={this.size} height={this.size}/><br/>              
                Coupon title:{this.props.coupon.title}<br/>
                Coupon category:{this.props.coupon.category}<br/>                
                {
                    this.state.showMore && 
                    <>
                    Coupon description:{this.props.coupon.description}<br/>
                    Coupon price:{this.props.coupon.price}<br/>
                    Coupon start date :{this.props.coupon.startDate}<br/>
                    Coupon end date:{this.props.coupon.endDate}<br/>  
                    Coupon amount :{this.props.coupon.amount}<br/>
                    Coupon id:{this.props.coupon.id}<br/>
                    </>
                }
               <br/>
               
                {/*Coupon purchases:{this.props.coupon.purchases}<br/>*/}
                <ButtonGroup variant="contained" fullWidth>
                    {this.props.showDelete && <Button color="secondary" onClick={this.deleteCoupon}>Delete</Button>}
                    <Button color="primary" onClick={this.showMe}>More info</Button>
                </ButtonGroup>
            </div>
        );
    }
}

export default SingleCoupon_CC;
