import { Button, ButtonGroup, TextField } from "@material-ui/core";
import { Component, SyntheticEvent } from "react";
import Coupon from "../../../Models/Coupon";
import Customer from "../../../Models/Customer";
import jwtAxios from "../../../utils/JwtAxios";
import SingleCoupon_CC from "../../admin/src/Components/singleCoupon_CC/singleCoupon_CC";
import "./getOneCustomerCouponsUpToPrice_CC.css";

interface GetOneCustomerCouponsUpToPrice_CCProps {
	
}

interface GetOneCustomerCouponsUpToPrice_CCState {
    price:number,
	customerNumber:string,
    customer: Customer,
    show:boolean,
    coupons:Coupon[]
}

class GetOneCustomerCouponsUpToPrice_CC extends Component<GetOneCustomerCouponsUpToPrice_CCProps, GetOneCustomerCouponsUpToPrice_CCState> {
    private setValue = (args: SyntheticEvent) => {
        //const value=args.target.value; //who is args??? what is it type???
        const value =(args.target as HTMLInputElement).value;
        this.setState({customerNumber: value});
    };
    private getCustomer=(args:SyntheticEvent)=>{
        console.log(this.state.customerNumber)
        console.log(this.state.price)

        jwtAxios.get("http://localhost:8080/customer/getOneCustomerCouponsUpToPrice/"+this.state.customerNumber+"/"+"{price}?price=" +this.state.price)
        .then((response)=>{
            console.log("my data:")
            console.log(response);
            this.setState({show:true});
            this.setState({coupons:response.data})
        })
        .catch((error)=>{
            this.setState({show:false});
            console.log(error);
        })
    }
    public constructor(props: GetOneCustomerCouponsUpToPrice_CCProps) {
        super(props);
        this.state = {
            price:0,
			customerNumber:"",
            customer:new Customer(),
            show:false,
            coupons:[]
        };
    }
    private getPrice=(args:SyntheticEvent)=>{
        const value = parseInt((args.target as HTMLInputElement).value);
        this.setState({price: value});
    }
    public render(): JSX.Element {
        return (
            <div className="getOneCustomerCouponsUpToPrice_CC">
                 <h1> Get One customer coupons up to price</h1><br/>
				<TextField label="customer number" variant="outlined" onChange={this.setValue} value={this.state.customerNumber}/>
                <TextField label="top price" variant="outlined" onChange={this.getPrice} value={this.state.price}/>
                <ButtonGroup variant="contained" onClick={this.getCustomer} >
                    <Button color="primary">Search</Button>
                </ButtonGroup>
                {
				this.state.coupons.map(item =>
                        <SingleCoupon_CC key={item.id} coupon={item} showDelete={false}/>
                    )}
            </div>
        );
    }
}

export default GetOneCustomerCouponsUpToPrice_CC;
