import { Button, ButtonGroup, MenuItem, Select, TextField } from "@material-ui/core";
import { Component, SyntheticEvent } from "react";
import Coupon from "../../../Models/Coupon";
import Customer from "../../../Models/Customer";
import jwtAxios from "../../../utils/JwtAxios";
import SingleCoupon_CC from "../../admin/src/Components/singleCoupon_CC/singleCoupon_CC";
import "./getOneCustomerCouponsByCategory_CC.css";

interface GetOneCustomerCouponsByCategory_CCProps {
	
}

interface GetOneCustomerCouponsByCategory_CCState {
    category:string,
	customerNumber:number,
    customer: Customer,
    show:boolean,
    coupons:Coupon[]
}

class GetOneCustomerCouponsByCategory_CC extends Component<GetOneCustomerCouponsByCategory_CCProps, GetOneCustomerCouponsByCategory_CCState> {

    private setValue = (args: SyntheticEvent) => {
        const value = (parseInt)((args.target as HTMLInputElement).value);
        this.setState({customerNumber: value});
    };
    private getCustomer=(args:SyntheticEvent)=>{
        jwtAxios.get("http://localhost:8080/customer/getOneCustomerCouponsByCategory/"+this.state.customerNumber+"/"+"{category}?category="+this.state.category)
        .then((response)=>{
            console.log("my data:")
            console.log(response);
            this.setState({show:true});
            this.setState({coupons:response.data})
        })
        .catch((error)=>{
            this.setState({show:false});
            console.log(error);
        })
    }
    public constructor(props: GetOneCustomerCouponsByCategory_CCProps) {
        super(props);
        this.state = {
            category:"",
			customerNumber:0,
            customer:new Customer(),
            show:false,
            coupons:[]
        };
    }
    private categoryValue = (args:any) =>{
        console.log("my cat:"+args);
        const cat=(args.target as HTMLInputElement).value;
        console.log(cat);
        this.setState({category:cat})
    }

    public render(): JSX.Element {
        return (
            <div className="getOneCustomerCouponsByCategory_CC">
					  <h1> Get One customer coupons by category</h1><br/>
				<TextField label="customer number" variant="outlined" onChange={this.setValue} value={this.state.customerNumber}/><br/>
                <Select value={this.state.category} onChange={this.categoryValue} label="Category" labelWidth={100}>
                    <MenuItem value={"Food"}>Food</MenuItem>
                    <MenuItem value={"Clothing"}>Clothing</MenuItem>
                    <MenuItem value={"Vacation"}>Vacation</MenuItem>
                    <MenuItem value={"Electricity"}>Electricity</MenuItem>
                    <MenuItem value={"Sports"}>Sports</MenuItem>
                </Select><br/><br/>
                <ButtonGroup variant="contained" onClick={this.getCustomer} >
                    <Button color="primary">Search</Button>
                </ButtonGroup>
                {this.state.coupons.map(item =>
                        <SingleCoupon_CC key={item.id} coupon={item} showDelete={false}/>)}
            </div>
        );
    }
}

export default GetOneCustomerCouponsByCategory_CC;
