import { Button, ButtonGroup, TextField } from "@material-ui/core";
import { Component, SyntheticEvent } from "react";
import Company from "../../../Models/Company";
import Coupon from "../../../Models/Coupon";
import jwtAxios from "../../../utils/JwtAxios";
import SingleCoupon_CC from "../../admin/src/Components/singleCoupon_CC/singleCoupon_CC";
import "./getOneCompanyCoupons_CC.css";

interface GetOneCompanyCoupons_CCProps {
	
}

interface GetOneCompanyCoupons_CCState {
    companyNumber:string,
    company:Company,
    show:boolean,
	coupon:Coupon[];
}


class GetOneCompanyCoupons_CC extends Component<GetOneCompanyCoupons_CCProps, GetOneCompanyCoupons_CCState> {
    private setValue = (args: SyntheticEvent) => {
        //const value=args.target.value; //who is args??? what is it type???
        const value = (args.target as HTMLInputElement).value;
        this.setState({companyNumber: value});
    };
    
    private getCompany = (args: SyntheticEvent) => {
        jwtAxios.get("http://localhost:8080/administrator/getOneCompany/"+this.state.companyNumber)
        .then((response)=>{
            this.setState({show:true});
            this.setState({coupon:response.data.coupons});
        })
        .catch((error)=>{
            this.setState({show:false});
            console.log(error);
        })
    }
    public constructor(props: GetOneCompanyCoupons_CCProps) {
        super(props);
        this.state = {
            companyNumber:"",
            company:new Company(),
            show:false,
			coupon:[]
        };
    }

    public render(): JSX.Element {
        return (
            <div className="getOneCompanyCoupons_CC">
                  <h1>Get single company coupons</h1><hr/>
				{/*<input type="text" placeholder="company number..." onChange={this.setValue} value={this.state.companyNumber}/>*/}
                <TextField label="company number" variant="outlined" onChange={this.setValue} value={this.state.companyNumber}/>
                <ButtonGroup variant="contained" onClick={this.getCompany} >
                    <Button color="primary">Search</Button>
                </ButtonGroup>
                <br/>
                <span> get all company coupons</span>
                {
				this.state.coupon.map(item =>
                        <SingleCoupon_CC key={item.id} coupon={item} showDelete={true}/>
                    )}
            </div>
        );
    }
}

export default GetOneCompanyCoupons_CC;
