import { Button, ButtonGroup, TextField } from "@material-ui/core";
import axios from "axios";
import { Component, SyntheticEvent } from "react";
import Company from "../../../Models/Company";
import Coupon from "../../../Models/Coupon";
import jwtAxios from "../../../utils/JwtAxios";
import SingleCoupon_CC from "../../admin/src/Components/singleCoupon_CC/singleCoupon_CC";
import "./getAllCompanyCouponsUpToPrice_CC.css";

interface GetAllCompanyCouponsUpToPrice_CCProps {
	
}

interface GetAllCompanyCouponsUpToPrice_CCState {
    price:number,
	companyNumber:string,
    company:Company,
    show:boolean,
    coupon:Coupon[],
}

class GetAllCompanyCouponsUpToPrice_CC extends Component<GetAllCompanyCouponsUpToPrice_CCProps, GetAllCompanyCouponsUpToPrice_CCState> {

    private setValue = (args: SyntheticEvent) => {
        //const value=args.target.value; //who is args??? what is it type???
        const value = (args.target as HTMLInputElement).value;
        this.setState({companyNumber: value});
    };
    private getCompanyCoupons = (args: SyntheticEvent) => {
        jwtAxios.get("http://localhost:8080/company/getAllCompanyCouponsUpToPrice/"+this.state.companyNumber+"/"+this.state.price)
        .then((response)=>{
            this.setState({show:true});
            console.log(response.data)
            this.setState({coupon:response.data});
            
        })
        .catch((error)=>{
            this.setState({show:false});
            console.log(error);
        })
    }
    public constructor(props: GetAllCompanyCouponsUpToPrice_CCProps) {
        super(props);
        this.state = {
            price:0,
			companyNumber:"",
            company:new Company(),
            show:false,
			coupon:[]
        };
    }
    private getPrice=(args:SyntheticEvent)=>{
        const value = parseInt((args.target as HTMLInputElement).value);
        this.setState({price: value});
    }

    public render(): JSX.Element {
        return (
            <div className="getAllCompanyCouponsUpToPrice_CC">
				<TextField label="company number" variant="outlined" onChange={this.setValue} value={this.state.companyNumber}/>
                <TextField label="top price" variant="outlined" onChange={this.getPrice} value={this.state.price}/>
                <ButtonGroup variant="contained" onClick={this.getCompanyCoupons} >
                    <Button color="primary">Search</Button>
                </ButtonGroup>
                {
				this.state.coupon.map(item =>
                        <SingleCoupon_CC key={item.id} coupon={item} showDelete={true}/>
                    )}
            </div>
        );
    }
}

export default GetAllCompanyCouponsUpToPrice_CC;
