import axios from "axios";

 
//יצירת אובייקט מוכן של
//axios => singleTon
const jwtAxios = axios.create();
 
//Request Interceptor - מה אנחנו רוצים לבצע מראש בעת שליחת בקשה לשרת
jwtAxios.interceptors.request.use(request => {
    console.log("my token to send:");
    console.log(localStorage.getItem("token"))
    request.headers = {
        "Authorization": localStorage.getItem("token"),
    }
    return request;
});
 
//insert here interceptor of response with header authorization
 
jwtAxios.interceptors.response.use(response=>{
    console.log("local storage:");
    console.log(response.headers.autorization);
    localStorage.setItem("token", response.headers.autorization);
    return response;
})
 
export default jwtAxios;