import { Component } from "react";
import Coupon from "../../../Models/Coupon";
import jwtAxios from "../../../utils/JwtAxios";
import SingleCoupon_CC from "../src/Components/singleCoupon_CC/singleCoupon_CC";

import "./getAllCoupons.css";

interface GetAllCoupons_CCProps {
	
}

interface GetAllCoupons_CCState {
	coupon:Coupon[];
}

class GetAllCoupons_CC extends Component<GetAllCoupons_CCProps, GetAllCoupons_CCState> {

    public constructor(props: GetAllCoupons_CCProps) {
        super(props);
        this.state = {
			coupon:[]
        };
    }

    public render(): JSX.Element {
        return (
            <div className="getAllCoupons_CC">
				Get all coupons<hr/>
                {this.state.coupon.map(item=>
                    <SingleCoupon_CC key={item.id} coupon={item} showDelete={false}/>)}
            </div>
        );
    }
    public componentDidMount(){
        jwtAxios.get("http://localhost:8080/getAllCoupons")
        .then ((response)=>{
            console.log(response.data);
            this.setState({coupon:response.data}); 
            //console.log(this.state.coupon);
        })
        .catch((error)=>{
            console.log(error);
        })
    }

}

export default GetAllCoupons_CC;
