import { Button, ButtonGroup, TextField } from "@material-ui/core";
import { Component, SyntheticEvent } from "react";
import Coupon from "../../../Models/Coupon";
import Customer from "../../../Models/Customer";
import jwtAxios from "../../../utils/JwtAxios";
import "./addCouponPurchase_CC.css";

interface AddCouponPurchase_CCProps {
	
}

interface AddCouponPurchase_CCState {
    couponNumber:string,
	customerNumber:string,
    customer: Customer,
    coupon:Coupon,
    show:boolean,
}

class AddCouponPurchase_CC extends Component<AddCouponPurchase_CCProps, AddCouponPurchase_CCState> {

    public constructor(props: AddCouponPurchase_CCProps) {
        super(props);
        this.state = {
            couponNumber:"",
			customerNumber:"",
            customer: new Customer(),
            coupon: new Coupon(),
            show:false
        };
    }

    private setValue = (args: SyntheticEvent) => {
        const value = (args.target as HTMLInputElement).value;
        this.setState({customerNumber: value});
    };
    private getCustomer=()=>{
        console.log("test data:");
        console.log(this.state.coupon);
        jwtAxios.post("http://localhost:8080/customer/addCouponPurchase",this.state.coupon)
        .then((response)=>{
            console.log("my data:")
            console.log(response);
            this.setState({show:true});
            this.setState({customer:response.data})
        })
        .catch((error)=>{
            this.setState({show:false});
            console.log(error);
        })
    }
    private setValue1 = (args: SyntheticEvent) => {
        const value =(args.target as HTMLInputElement).value;
        this.setState({couponNumber: value});
        
    };
    private getCoupon=()=>{
        console.log("coupon number:");
        console.log(this.state.couponNumber);
        
        jwtAxios.get("http://localhost:8080/administrator/getOneCoupon/"+this.state.couponNumber)
        .then((response)=>{
            console.log("get coupon:");
            console.log(response);
            this.setState({show:true});
            this.setState({coupon:response.data})
            console.log(this.state.coupon);
        })
        .catch((error)=>{
            this.setState({show:false});
            console.log(error);
        })
        
    }

   private makePurchase=()=>{
        this.getCoupon();
      
        //this.getCustomer();
   }
   

    public render(): JSX.Element {
        return (
            <div className="addCouponPurchase_CC">
				  <h1> add coupon purchase</h1><br/>
				<TextField label="customer number" variant="outlined" onChange={this.setValue} value={this.state.customerNumber}/><br/><br/>
                <TextField label="coupon number" variant="outlined" onChange={this.setValue1} value={this.state.couponNumber}/><br/><br/>
                <ButtonGroup variant="contained">
                    <Button color="primary" onClick={this.makePurchase}>purchase</Button>
                </ButtonGroup>
            </div>
        );
    }
}

export default AddCouponPurchase_CC;
