import { Button, ButtonGroup } from "@material-ui/core";
import {  SyntheticEvent } from "react";
import { Component } from "react";
import Company from "../../../Models/Company";
import jwtAxios from "../../../utils/JwtAxios";
import SingleCoupon_CC from "../src/Components/singleCoupon_CC/singleCoupon_CC";
import "./singleCompany_CC.css";

interface SingleCompany_CCProps {
	company:Company;
}

interface SingleCompany_CCState {
    
}

class SingleCompany_CC extends Component<SingleCompany_CCProps, SingleCompany_CCState> {
    
    public constructor(props: SingleCompany_CCProps) {
        super(props);
        this.state = {
			
        };
    }
 
    private deleteCompany = (args: SyntheticEvent) => {
        //add prompt for asking if we are sure to delete
        const url ="http://localhost:8080/administrator/deleteCompany/";
        jwtAxios.get(url+this.props.company.companyid)
        .then((response)=>{
            this.setState({company:response.data})
        })
        .catch((error)=>{
            console.log(error);
        })
    }
    private updateCompany=(args:SyntheticEvent)=>{
        const url1="http://localhost:8080/administrator/updateCompany";
        jwtAxios.post(url1+this.props.company.companyid)
        .then((respons)=>{
            console.log(respons.data)
        })
        .catch((error)=>{
            console.log(error);
        })
    }
    
    public render(): JSX.Element {
        return (
            <div className="singleCompany_CC Box">
				Company id:{this.props.company.companyid}<br/>
                Company email:{this.props.company.email}<br/>
                Company name:{this.props.company.name}<br/>
                Company Password:{this.props.company.password}<br/>
                {this.props.company.coupons.map(item=>
                <SingleCoupon_CC key={item.id} coupon={item} showDelete={true}/>)}
                <ButtonGroup variant="contained" fullWidth>
                    <Button color="secondary" onClick={this.deleteCompany}>Delete</Button>
                    <Button color="primary" onClick={this.updateCompany}>Update</Button>
                </ButtonGroup>
                
            </div>
        );
    }
}

export default SingleCompany_CC;
