import { Button, ButtonGroup, TextField } from "@material-ui/core";
import { useForm } from "react-hook-form";

import Company from "../../../Models/Company";
import jwtAxios from "../../../utils/JwtAxios";
import "./addCompany_FC.css";

function AddCompany_FC(): JSX.Element {
    const { register, handleSubmit, watch, formState: { errors } } = useForm();

    function sendDetails(companyDetails: Company){
        console.log(companyDetails)
        
        jwtAxios.post("http://localhost:8080/administrator/addCompany",companyDetails)
       .then(()=>{
            console.log("company was add");
            console.log(companyDetails);
            }
       )
       .catch(error=>{
        console.log(error.response)
       })
       
    }

    return (
        <div className="addCompany_FC">
			<h1> Add Compnay </h1>
            <form onSubmit={handleSubmit(sendDetails)}>
                <TextField  label="company name" variant="filled" {...register("name")} /><br/><br/>
                <TextField  label="company Email" variant="filled" {...register("email")}/><br/><br/>
                <TextField  label="company password" variant="filled" {...register("password")}/><br/><br/>
                <ButtonGroup variant="contained" fullWidth>
                    <Button color="primary" type="submit">Add Company</Button>
                </ButtonGroup>
            </form>
        </div>
    );
}

export default AddCompany_FC;
