import { Button, ButtonGroup, TextField } from "@material-ui/core";
import axios from "axios";
import { SyntheticEvent, useState } from "react";
import { useForm } from "react-hook-form";
import Customer from "../../../Models/Customer";
import jwtAxios from "../../../utils/JwtAxios";
import "./updateCustomer_FC.css";

interface UpdateCustomer_FCProps {
    customer: Customer;
}
function UpdateCustomer_FC(): JSX.Element {
    const [customer, setCustomer] = useState(new Customer());
    const [customerID, setCustomerID] = useState(0);
    const { register, handleSubmit, watch, formState: { errors } } = useForm();
    function handleChange(args: SyntheticEvent) {
        var newValue = (args.target as HTMLInputElement).value;
        setCustomerID(parseInt(newValue));
    }
    function getCustomer() {
        jwtAxios.get<Customer>("http://localhost:8080/administrator/getOneCustomer/" + customerID)
            .then(response => { setCustomer(response.data) })
            .catch(err => { })
    }
    function sendDetails(customerDetails: Customer) {
        
        var updatedCustomer = customer;
        updatedCustomer.first_name = customerDetails.first_name;
        updatedCustomer.last_name=customerDetails.last_name;
        updatedCustomer.password = customerDetails.password;
        console.log(updatedCustomer)
        jwtAxios.post("http://localhost:8080/administrator/updateCustomer",updatedCustomer)
            .then(() => {
                console.log("customer was update");
                console.log(customerDetails);
               
            }
            )
            .catch(error => {
                console.log(error.response)
            })

    }
    return (
        <div className="updateCustomer_FC">
            <h4>Customer Update </h4> <hr />
            <br />
            <TextField label="customer number" variant="outlined" onChange={handleChange} value={customerID} />
            <br/>
            <ButtonGroup variant="contained" onClick={getCustomer} >
                
                <Button color="primary">Search</Button>
            </ButtonGroup>
            <br/><br/>
                Customer id
                <br />
                <TextField value={customer.id} disabled /><br /><br />
                Customer email
                <br />
                <TextField value={customer.email} disabled /><br /><br />
                Customer first name<br />
                <TextField onChange={customer.first_name} defaultValue={customer.first_name}   {...register("first_name")} /><br /><br />
                Customer last name<br />
                <TextField onChange={customer.last_name} defaultValue={customer.last_name}   {...register("last_name")} /><br /><br />
                Customer Password<br />
                <TextField onChange={customer.password} defaultValue={customer.password} {...register("password")} /><br /><br />
                <ButtonGroup variant="contained" onClick={handleSubmit(sendDetails)} >
                    <Button color="secondary">Update</Button>
                </ButtonGroup>
            
           
        </div>
    );
}

export default UpdateCustomer_FC;
