
import { Button, ButtonGroup } from "@material-ui/core";
import { Component, SyntheticEvent } from "react";
import Customer from "../../../../../Models/Customer";
import jwtAxios from "../../../../../utils/JwtAxios";
import SingleCoupon_CC from "../singleCoupon_CC/singleCoupon_CC";
import "./singleCustomer_CC.css";

interface SingleCustomer_CCProps {
	customer:Customer;
    
}

interface SingleCustomer_CCState {
	
}

class SingleCustomer_CC extends Component<SingleCustomer_CCProps, SingleCustomer_CCState> {

    public constructor(props: SingleCustomer_CCProps) {
        super(props);
        this.state = {
			
        };

    }
    private deleteCustomer = (args: SyntheticEvent) =>{
        const url="http://localhost:8080/administrator/deleteCustomer/";
        jwtAxios.get(url+this.props.customer.id)
            .then((response)=>{
                this.setState({customer:response.data})
            })
            .catch((error)=>{
                console.log(error);
            })
    }
    public render(): JSX.Element {
        return (
            
            <div className="singleCustomer_CC Box">
                Customer id:{this.props.customer.id}<br/>
                Customer first name:{this.props.customer.first_name}<br/>
                Customer last name :{this.props.customer.last_name}<br/>
                Customer email:{this.props.customer.email}<br/>
                Customer password:{this.props.customer.password}<br/>
                {this.props.customer.coupons.map(item=>
                    <SingleCoupon_CC key={item.id} coupon={item}  showDelete={false}/>)}
                <ButtonGroup variant="contained" fullWidth>
                    <Button color="secondary" onClick={this.deleteCustomer}>Delete</Button>
                </ButtonGroup>
				
            </div>
        );
    }

   
}

export default SingleCustomer_CC;
