import { Button, ButtonGroup, TextField } from "@material-ui/core";
import { Component, SyntheticEvent } from "react";
import Coupon from "../../../../../Models/Coupon";
import jwtAxios from "../../../../../utils/JwtAxios";
import SingleCoupon_CC from "../singleCoupon_CC/singleCoupon_CC";
import "./getOneCoupon_CC.css";


interface GetOneCoupon_CCProps {
	
}

interface GetOneCoupon_CCState {
	couponNumber:string,
    coupon: Coupon,
    show:boolean
}

class GetOneCoupon_CC extends Component<GetOneCoupon_CCProps, GetOneCoupon_CCState> {

    public constructor(props: GetOneCoupon_CCProps) {
        super(props);
        this.state = {
			couponNumber:"",
            coupon:new Coupon(),
            show:false
        };
    }
    private setValue = (args: SyntheticEvent) => {
        const value = (args.target as HTMLInputElement).value;
        this.setState({couponNumber: value});
    };
    private getCoupon=(args:SyntheticEvent)=>{
        jwtAxios.get("http://localhost:8080/administrator/getOneCoupon/"+this.state.couponNumber)
        .then((response)=>{
            this.setState({show:true});
            this.setState({coupon:response.data})
            console.log(this.state.coupon);
        })
        .catch((error)=>{
            this.setState({show:false});
            console.log(error);
        })
    }
    public render(): JSX.Element {
        return (
            <div className="getOneCoupon_CC">
				  <h1> Get one coupon</h1><br/>
				<TextField label="coupon number" variant="outlined" onChange={this.setValue} value={this.state.couponNumber}/>
                <ButtonGroup variant="contained" onClick={this.getCoupon} >
                    <Button color="primary">Search</Button>
                </ButtonGroup>
                {this.state.show && <SingleCoupon_CC coupon={this.state.coupon} showDelete={true}/>}
            </div>
        );
    }
}

export default GetOneCoupon_CC;
