import { Button, ButtonGroup, MenuItem, Select, TextField } from "@material-ui/core";
import { SyntheticEvent, useState } from "react";
import { useForm } from "react-hook-form";
import Coupon from "../../../Models/Coupon";
import jwtAxios from "../../../utils/JwtAxios";
import "./updateCoupon_FC.css";

interface UpdateCoupon_FCProps{
    coupon:Coupon;
}

function UpdateCoupon_FC(): JSX.Element {
    const [coupon,setCoupon]=useState(new Coupon());
    const [couponID, setCouponID]=useState(0);
    const {register, handleSubmit,watch,formState:{errors}}=useForm();
    function handleChange(args: SyntheticEvent) {
        var newValue = (args.target as HTMLInputElement).value;
        
        setCouponID(parseInt(newValue));
    }
    function getCoupon() {
        jwtAxios.get<Coupon>("http://localhost:8080/administrator/getOneCoupon/" + couponID)
            .then(response => { setCoupon(response.data) })
            .catch(err => { })
    }
    const [myCat,setCat] = useState();
    function sendDetails(couponDetails: Coupon) {
        
        var updatedCoupon = coupon;
        updatedCoupon.amount = couponDetails.amount;
        updatedCoupon.description = couponDetails.description;
        updatedCoupon.price=couponDetails.price;
        updatedCoupon.title=couponDetails.title;
        /*updatedCoupon.category=myCat;
        updatedCoupon.image=updatedCoupon.image;*/
        
        console.log(updatedCoupon)
        jwtAxios.post("http://localhost:8080/company/updateCoupon",updatedCoupon)
            .then(() => {
                console.log("coupon was update");
                console.log(couponDetails);
               
            }
            )
            .catch(error => {
                console.log(error.response)
            })

    } 
    const handleCat = (event:any) => {setCat(event.target.value)};
    return (
        <div className="updateCoupon_FC">
            <h4>Coupon Update</h4>
            <hr/>
			<br />
            <TextField label="coupon number" variant="outlined" onChange={handleChange} value={couponID} /><br/>
            <ButtonGroup variant="contained" onClick={getCoupon} >
                <Button color="primary">Search</Button>
            </ButtonGroup>
            <br/>
            Coupon id<br/>
            <TextField value={coupon.id} disabled/><br/><br/>
            Coupon companyid<br/>
            <TextField value={coupon.companyid} disabled/><br/><br/>
            Coupon title<br/>
            <TextField onChange={coupon.title} defaultValue={coupon.title} {...register("title")} /><br /><br />
            Coupon descreiption<br/>
            <TextField onChange={coupon.description} defaultValue={coupon.description}{...register("description")} /><br /><br />
            Coupon price<br/>
            <TextField onChange={coupon.price} defaultValue={coupon.price}{...register("price")} /><br /><br />
            Coupon category<br/>{/* todo*/}
            
            <Select value={myCat} onChange={handleCat} label="Category" labelWidth={100}>
                    <MenuItem value={"Food"}>Food</MenuItem>
                    <MenuItem value={"Clothing"}>Clothing</MenuItem>
                    <MenuItem value={"Vacation"}>Vacation</MenuItem>
                    <MenuItem value={"Electricity"}>Electricity</MenuItem>
                    <MenuItem value={"Sports"}>Sports</MenuItem>
                </Select><br/><br/>            Coupon startDate<br/>
            <TextField  type="date" label="coupon startDate" variant="filled" {...register("startDate")}  /><br/><br/>
            Coupon endDate<br/>
            <TextField  type="date" label="coupon endDate" variant="filled" {...register("endDate")}/><br/><br/>


            <ButtonGroup variant="contained" onClick={handleSubmit(sendDetails)} >
                    <Button color="secondary">Update</Button>
                </ButtonGroup>
        </div>
    );
}

export default UpdateCoupon_FC;
