import { Button, ButtonGroup, TextField } from "@material-ui/core";
import { SyntheticEvent, useState } from "react";
import { useForm } from "react-hook-form";
import Company from "../../../Models/Company";
import jwtAxios from "../../../utils/JwtAxios";
import "./updateCompany_FC.css";

interface UpdateCompany_FCProps {
	company:Company;
}


function UpdateCompany_FC(props: UpdateCompany_FCProps): JSX.Element {
    const [company,setCompnay]=useState(new Company());
    const [companyID,setCompanyID] = useState(0);
    const { register, handleSubmit, watch, formState: { errors } } = useForm();

    
    function handleChange(args:SyntheticEvent){
        var newValue = (args.target as HTMLInputElement).value;
        setCompanyID(parseInt(newValue));
    }

    function getCompany(){
        jwtAxios.get<Company>("http://localhost:8080/administrator/getOneCompany/"+companyID)
        .then(response=>{setCompnay(response.data)})
        .catch(err=>{})
    }

    function sendDetails(companyDetails: Company){
        console.log(companyDetails)
        var updatedCompny=company;
        updatedCompny.name=companyDetails.name;
        updatedCompny.password=companyDetails.name;
        jwtAxios.post("http://localhost:8080/administrator/updateCompany",updatedCompny)
       .then(()=>{
            console.log("company was updated!!!");
            console.log(companyDetails);
            }
       )
       .catch(error=>{
        console.log(error.response)
       })
       
    }

    return (
        <div className="updateCompany_FC">
            <br/>
            <TextField label="company number" variant="outlined" onChange={handleChange} value={companyID}/><br/>
            <ButtonGroup variant="contained" onClick={getCompany} >
                <Button color="primary">Search</Button>
            </ButtonGroup>
            <hr/>
            <br/><br/>
                Companyr id
                <br />
                <TextField value={company.companyid} disabled /><br /><br />
                Company email
                <br />
                <TextField value={company.email} disabled /><br /><br />
                Company name<br />
                <TextField onChange={company.name} defaultValue={company.name}   {...register("name")} /><br /><br />
                Company Password<br />
                <TextField onChange={company.password} defaultValue={company.password} {...register("password")} /><br /><br />
                <ButtonGroup variant="contained" onClick={handleSubmit(sendDetails)} >
                    <Button color="secondary">Update</Button>
                </ButtonGroup>
        </div>
    );
}

export default UpdateCompany_FC;
