import { Component } from "react";
import Customer from "../../../Models/Customer";
import jwtAxios from "../../../utils/JwtAxios";
import SingleCustomer_CC from "../src/Components/singleCustomer_CC/singleCustomer_CC";


import "./getAllCustomers_CC.css";

interface GetAllCustomers_CCProps {
	
}

interface GetAllCustomers_CCState {
	customer:Customer[]
}

class GetAllCustomers_CC extends Component<GetAllCustomers_CCProps, GetAllCustomers_CCState> {

    public constructor(props: GetAllCustomers_CCProps) {
        super(props);
        this.state = {
			customer:[],
        };
    }

    public render(): JSX.Element {
        return (
            <div className="getAllCustomers_CC">
				Get all customers<hr/>
                {this.state.customer.map(item=>
                    <SingleCustomer_CC key={item.id} customer={item} />)}
            </div>
        );
    }
    public componentDidMount(){
        jwtAxios.post("http://localhost:8080/administrator/getAllCustomers")
    .then ((response)=>{
        console.log(response);
        this.setState({customer:response.data}); 
        console.log(this.state.customer);
    })
    .catch((error)=>{
        console.log(error);
    })
}
}


export default GetAllCustomers_CC;


