import { Route, Switch } from "react-router";
import AddCompany_FC from "../../controllers/admin/addCompany_FC/addCompany_FC";
import AddCustomer_FC from "../../controllers/admin/addCustomer_FC/addCustomer_FC";
import GetAllCompanies_CC from "../../controllers/admin/getAllCompanies_CC/getAllCompanies_CC";
import GetAllCoupons_CC from "../../controllers/admin/getAllCoupons_CC/getAllCoupons";
import GetAllCustomers_CC from "../../controllers/admin/getAllCustomers_CC/getAllCustomers_CC";
import GetOneCompany_CC from "../../controllers/admin/getOneCompany_CC/getOneCompany_CC";
import GetOneCoupon_CC from "../../controllers/admin/src/Components/getOneCoupon_CC/getOneCoupon_CC";
import GetOneCustomer_CC from "../../controllers/admin/src/Components/getOneCustomer_CC/getOneCustomer_CC";
import UpdateCompany_FC from "../../controllers/admin/updateCompany_FC/updateCompany_FC";
import UpdateCustomer_FC from "../../controllers/admin/updateCustomer_FC/updateCustomer_FC";
import AddCoupon_FC from "../../controllers/company/addCoupon_FC/addCoupon_FC";
import GetAllCompanyCouponsUpToPrice_CC from "../../controllers/company/getAllCompanyCouponsUpToPrice_CC/getAllCompanyCouponsUpToPrice_CC";
import GetAllCouponsByCompanyAndCategory_CC from "../../controllers/company/getAllCouponsByCompanyAndCategory_CC/getAllCouponsByCompanyAndCategory_CC";
import GetOneCompanyCoupons_CC from "../../controllers/company/getOneCompanyCoupons_CC/getOneCompanyCoupons_CC";
import UpdateCoupon_FC from "../../controllers/company/updateCoupon_FC/updateCoupon_FC";
import AddCouponPurchase_CC from "../../controllers/customer/addCouponPurchase_CC/addCouponPurchase_CC";
import GetOneCustomerCouponsByCategory_CC from "../../controllers/customer/getOneCustomerCouponsByCategory_CC/getOneCustomerCouponsByCategory_CC";
import GetOneCustomerCouponsUpToPrice_CC from "../../controllers/customer/getOneCustomerCouponsUpToPrice_CC/getOneCustomerCouponsUpToPrice_CC";
import GetOneCustomerCoupons_CC from "../../controllers/customer/getOneCustomerCoupons_CC/getOneCustomerCoupons_CC";
import Login_FC from "../../controllers/login_FC/login_FC";

function Routing(): JSX.Element {
    return (
        <div className="Routing">
            <Switch>
               <Route path="/addCompany" component={AddCompany_FC} exact/>
               <Route path="/addCustomer" component={AddCustomer_FC} exact/>
               <Route path="/getOneCompany" component={GetOneCompany_CC} exact/>
               <Route path="/getOneCustomer" component={GetOneCustomer_CC} exact/>
               <Route path="/getOneCoupon" component={GetOneCoupon_CC} exact/>
               <Route path="/getAllCompanies" component={GetAllCompanies_CC} exact/>
               <Route path="/getAllCustomer" component={GetAllCustomers_CC} exact/>
               <Route path="/getAllCoupons" component={GetAllCoupons_CC} exact/>
               <Route path="/updateCompany" component={UpdateCompany_FC} exact/>
               <Route path="/updateCustomer" component={UpdateCustomer_FC} exact/>
               <Route path="/addCoupon" component={AddCoupon_FC} exact/>
               <Route path="/getAllCompanyCouponsUpToPrice" component={GetAllCompanyCouponsUpToPrice_CC} exact/>
               <Route path="/getAllCouponsByCompanyAndCategory" component={GetAllCouponsByCompanyAndCategory_CC} exact/>
               <Route path="/getOneCompany" component={GetOneCompany_CC} exact/>
               <Route path="/getOneCompanyCoupons" component={GetOneCompanyCoupons_CC} exact/>
               <Route path="/addCouponPurchase" component={AddCouponPurchase_CC} exact/>
               <Route path="/getOneCustomerCoupons" component={GetOneCustomerCoupons_CC} exact/>
               <Route path="/getOneCustomerCouponsByCategory" component={GetOneCustomerCouponsByCategory_CC} exact/>
               <Route path="/getOneCustomerCouponsUpToPrice" component={GetOneCustomerCouponsUpToPrice_CC} exact/>
               {/*<Route path="/getOneCoupon/:id" render={(props)=><FullCouponDetails_FC id={props.match.params.id}/>} exact/>*/}
               <Route path="/updateCoupon" component={UpdateCoupon_FC} exact/>
                <Route path="/login" component={Login_FC} exact />
            </Switch>

        </div>
    );
}

export default Routing;
