import { Button, ButtonGroup, MenuItem, Select, TextField } from "@material-ui/core";
import axios from "axios";
import { useState } from "react";
import { useForm } from "react-hook-form";
import Coupon from "../../../Models/Coupon";
import jwtAxios from "../../../utils/JwtAxios";
import "./addCoupon_FC.css";

function AddCoupon_FC(): JSX.Element {
    const { register, handleSubmit, watch, formState: { errors } } = useForm();

    //for holding the category from the form itself
    const [myCat,setCat] = useState();

    function sendDetails(couponDetails: Coupon){
        
       couponDetails.category=myCat;
        const url="http://localhost:8080/company/addCoupon";
        
        jwtAxios.post(url,couponDetails)
       .then(()=>{
            console.log("coupon was add");
            console.log(couponDetails);
            }
       )
       .catch(error=>{
          if (error.response){
              console.log("response"+error);
          } else if (error.request){
              console.log("request:"+error.request);
          } else if (error.message){
            console.log("error:"+error.message);
          }
       });
       
    }

    const handleChange = (event:any) => {setCat(event.target.value)};
    return (
        <div className="addCoupon_FC">
			<h1> Add Coupon </h1>
            <form onSubmit={handleSubmit(sendDetails)}>
                <TextField  label="coupon amount" variant="filled" {...register("amount")} /><br/><br/>
                {/* <TextField  label="coupon category" variant="filled" {...register("category")}/><br/><br/> */}
                <Select value={myCat} onChange={handleChange} label="Category" labelWidth={100}>
                    <MenuItem value={"Food"}>Food</MenuItem>
                    <MenuItem value={"Clothing"}>Clothing</MenuItem>
                    <MenuItem value={"Vacation"}>Vacation</MenuItem>
                    <MenuItem value={"Electricity"}>Electricity</MenuItem>
                    <MenuItem value={"Sports"}>Sports</MenuItem>
                </Select><br/><br/>
                <TextField  label="coupon description" variant="filled" {...register("description")}/><br/><br/>
                <TextField  type="date" label="coupon startDate" variant="filled" {...register("startDate")}/><br/><br/>
                <TextField  type="date" label="coupon endDate" variant="filled" {...register("endDate")}/><br/><br/>
                <TextField  label="coupon companyid" variant="filled" {...register("companyid")}/><br/><br/>
                <TextField  label="coupon image" variant="filled" {...register("image")}/><br/><br/>
                <TextField  label="coupon price" variant="filled" {...register("price")}/><br/><br/>
                <TextField  label="coupon title" variant="filled" {...register("title")}/><br/><br/>
                <ButtonGroup variant="contained" fullWidth>
                    <Button color="primary" type="submit">Add Coupon</Button>
                </ButtonGroup>
                </form>	
        </div>
    );
}

export default AddCoupon_FC;
