import { Button, ButtonGroup, TextField } from "@material-ui/core";
import { SyntheticEvent } from "react";
import { Component } from "react";
import Company from "../../../Models/Company";
import jwtAxios from "../../../utils/JwtAxios";
import SingleCompany_CC from "../singleCompany_CC/singleCompany_CC";

import "./getOneCompany_CC.css";

interface GetOneCompany_CCProps {
	
}

interface GetOneCompany_CCState {
    companyNumber:string,
    company:Company,
    show:boolean,
}

class GetOneCompany_CC extends Component<GetOneCompany_CCProps, GetOneCompany_CCState> {

    public constructor(props: GetOneCompany_CCProps) {
        super(props);
        this.state = {
			companyNumber:"",
            company:new Company(),
            show:false,
        };
    }

    private setValue = (args: SyntheticEvent) => {
        //const value=args.target.value; //who is args??? what is it type???
        const value = (args.target as HTMLInputElement).value;
        this.setState({companyNumber: value});
    };
    
    private getCompany = (args: SyntheticEvent) => {
        jwtAxios.get("http://localhost:8080/administrator/getOneCompany/"+this.state.companyNumber)
        .then((response)=>{
            console.log(response);
            this.setState({show:true});
            this.setState({company:response.data});
        })
        .catch((error)=>{
            this.setState({show:false});
            console.log(error);
        })
    }


    public render(): JSX.Element {
        return (
            <div className="getOneCompany_CC">
                <h1>Get single Company</h1><hr/>
				{/*<input type="text" placeholder="company number..." onChange={this.setValue} value={this.state.companyNumber}/>*/}
                <TextField label="company number" variant="outlined" onChange={this.setValue} value={this.state.companyNumber}/>
                <ButtonGroup variant="contained" onClick={this.getCompany} >
                    <Button color="primary">Search</Button>
                </ButtonGroup>
                {this.state.show && <SingleCompany_CC company={this.state.company}/>}
            </div>
        );
    }
}

export default GetOneCompany_CC;
