import { Component } from "react";
import Company from "../../../Models/Company";
import jwtAxios from "../../../utils/JwtAxios";
import SingleCompany_CC from "../singleCompany_CC/singleCompany_CC";
import "./getAllCompanies_CC.css";

interface GetAllCompanies_CCProps {
	
}

interface GetAllCompanies_CCState {
	company:Company[];
}

class GetAllCompanies_CC extends Component<GetAllCompanies_CCProps, GetAllCompanies_CCState> {

    public constructor(props: GetAllCompanies_CCProps) {
        super(props);
        this.state = {
			company:[],
        };
    }

    public render(): JSX.Element {
        return (
            <div className="getAllCompanies_CC">
				Get all companies<hr/>
                {
                    // we will do a map, to iterate on all companies that we will get from server
                    // we will take the company from the state
                    this.state.company.map(item =>
                        <SingleCompany_CC key={item.companyid} company={item}/>
                    )
                }
            </div>
        );
    }
    public componentDidMount(){
        //axios
        jwtAxios.post("http://localhost:8080/administrator/getAllCompanies")
        .then (response=>{
            console.log(response);
            this.setState({company:response.data}); 
            //getting the data from the state :)
            console.log(this.state.company);
        })
        .catch((error)=>{
            console.log(error);
        })
    }
}

export default GetAllCompanies_CC;
